import React, { useState } from 'react'
import clsx from 'clsx'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'

import CancelOutlinedIcon from '@material-ui/icons/CancelOutlined'
import TurnedInNotSharpIcon from '@material-ui/icons/TurnedInNotSharp'

import { FieldDef } from '../types'
// import MenuIcon from '@material-ui/icons/Menu'
import { makeStyles, Theme, StyleRules } from '@material-ui/core/styles'
// import log from 'loglevel'

import { util } from '@northarrowresearch/common'
import numeral from 'numeral'

interface Props {
    def: FieldDef
    val: any
    isPinned?: boolean
    onPinToggle: (pinToggle: string) => void
}

function dataPrint(k, val): React.ReactNode {
    if (val === null || val === undefined) {
        return <em>n/a</em>
    } else if (IDS.indexOf(k) > -1) return <code>{val}</code>
    else if (util.isNumeric(val)) {
        if (k === 'spheroid_dist') {
            return numeral(val).format('0.00000')
        }
        return util.formatNum(val)
    } else return val.toString()
}

// This is all here for some hacky nhdprops fields
const IDS = [
    'nhdplusid',
    'vpuid',
    'reachcode',
    'permanent_identifier',
    'fromnode',
    'tonode',
    'hydroseq',
    'levelpathi',
    'terminalpa',
    'dnlevelpat',
    'dnhydroseq'
]

const useStyles = makeStyles(
    (theme: Theme): StyleRules => ({
        root: {
            display: 'flex',
            width: '100%',
            overflow: 'hidden',
            borderBottom: '1px solid #DEDEDE'
        },
        rootHover: {
            background: '#eee'
        },
        inner: {
            display: 'flex',
            width: '100%',
            overflow: 'hidden'
            // '& > *': {
            //     border: '1px solid green'
            // }
        },
        noval: {
            color: '#999999'
        },
        pinned: {
            background: theme.palette.secondary.main,
            borderBottom: `1px solid ${theme.palette.secondary.main}`,
            color: theme.palette.secondary.contrastText
        },
        name: {
            padding: '3px',
            flex: '1 1 50%',
            textAlign: 'left',
            whiteSpace: 'nowrap',
            overflow: 'hidden',
            textOverflow: 'ellipsis'
        },
        value: {
            lineHeight: '1.5rem',
            padding: '3px',
            flex: '1 1 40%',
            textAlign: 'right'
        },
        unit: {
            lineHeight: '1.5rem',
            padding: '3px',
            paddingLeft: theme.spacing(1) / 2,
            flex: '0 1 10%',
            textAlign: 'left'
        },
        pinButton: {
            opacity: 0
        },
        buttonHover: {
            flex: '0 0',
            opacity: 1
        }
    })
)

const MetricRow: React.FC<Props> = ({ def, val, isPinned, onPinToggle }: Props) => {
    const classes = useStyles()
    const [isHovered, setHovered] = useState<boolean>(false)

    return (
        <div
            className={clsx(classes.root, { [classes.pinned]: isPinned, [classes.rootHover]: !isPinned && isHovered })}
            onMouseOver={(): void => setHovered(true)}
            onMouseOut={(): void => setHovered(false)}
            onDoubleClick={(): void => onPinToggle(def.metric_key)}
        >
            <Tooltip
                title={
                    <div>
                        <Typography>
                            {def.metric_name}
                            {def.units ? ` (${def.units})` : ''}
                        </Typography>
                        {def.description && <Typography>{def.description}</Typography>}
                    </div>
                }
            >
                <div
                    className={clsx(classes.inner, {
                        [classes.noval]: !isPinned && (val === undefined || val === null)
                    })}
                >
                    <Typography variant="body1" className={classes.name}>
                        {def.metric_name}
                    </Typography>
                    <Typography variant="body2" className={classes.value}>
                        {val === undefined ? 'No Value' : dataPrint(def.metric_key, val)}
                    </Typography>
                    <Typography variant="caption" className={classes.unit}>
                        {val === undefined ? '' : def.units}
                    </Typography>
                </div>
            </Tooltip>

            <Tooltip title={isPinned ? 'Unpin this metric' : 'Pin this matric'}>
                <IconButton
                    size="small"
                    className={clsx(classes.pinButton, { [classes.buttonHover]: isHovered })}
                    onClick={(): void => onPinToggle(def.metric_key)}
                    color="inherit"
                >
                    {isPinned ? <CancelOutlinedIcon /> : <TurnedInNotSharpIcon />}
                </IconButton>
            </Tooltip>
        </div>
    )
}

export default MetricRow
